<template>
  <div class="container">
    <br />
    <br />
    <br />
    <div class="row justify-content-center">
      <div class="col-md-8">
         <img src="https://www.hasnurgroup.com/assets/img/logo.png" style="" alt="">
         <br>
        <div class="card">
          <div class="card-header p-5 text-lg font-semibold">Login</div>
          <div class="card-body p-5">
            <div v-if="error" class="alert alert-danger">{{error}}</div>
            <p class="text-xs font-semibold p-3 text-red"></p>
            <form action="#" @submit.prevent="loginAuth">
              <div class="form-group row">
                <label for="email" class="col-md-4 col-form-label text-md-right">Email</label>
                <div class="col-md-6">
                  <input
                    id="email"
                    type="email"
                    class="form-control"
                    name="email"
                    value
                    required
                    autofocus
                    v-model="vdata.email"
                  />
                </div>
              </div>

              <div class="form-group row">
                <label for="password" class="col-md-4 col-form-label text-md-right">Password</label>

                <div class="col-md-6">
                  <input
                    id="password"
                    type="password"
                    class="form-control"
                    name="password"
                    required
                    v-model="vdata.password"
                  />
                </div>
              </div>

              <div class="form-group row mb-0">
                <div class="col-md-12 text-center">
                  <button type="submit" class="btn btn-primary font-times">
                    <span class="typcn typcn-key"></span> Login
                  </button>
                  <!-- <button type="button" @click="loginSdb" class="btn btn-primary font-times ml-2">
                    <span class="typcn typcn-key"></span> Login Sdb
                  </button> -->
                </div>
              </div>
              <br />
              <p class="text-center font-times">Atau login menggunakan</p>
              <br />
              <div v-if="cordova">
                <div class="col-md-12" @click="cordovaGoogleAuth">
                  <a class="btn btn-lg btn-outline-secondary text-sm btn-block" href="#">
                    <img
                      class="float-left pt-1"
                      src="https://img.icons8.com/color/16/000000/google-logo.png"
                    />Login Dengan Google
                  </a>
                </div>
              </div>
              <div v-else>
                <div class="col-md-12" @click="googleAuth">
                  <img 
                  style="width:200px;display:block;margin:auto;"
                  src="https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcSTAYxxDv8asU0Re49sZnHaqNpBvmn66Cun7i7V8hF9tDEUzLftYVeGkfs1aMbA02XIeQ&usqp=CAU" alt="">
                  <!-- <a class="btn btn-lg btn-outline-secondary text-sm btn-block" href="#">
                    <img
                      class="float-left pt-1"
                      src="https://img.icons8.com/color/16/000000/google-logo.png"
                    />Login Dengan Google
                  </a> -->
                </div>
              </div>
            </form>
            <!-- <button type="button" @click="facebookAuth" class="btn btn-sm btn-outline-dark text-green btn-block  "><span class="typcn typcn-social-facebook-circular"></span> Facebook</button> -->
            <br />
            <!-- <p
              class="text-center font-times font-bold"
              @click="$router.push('/auth/register')"
            >Daftar</p> -->
            <br />
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import firebase from "firebase";
let db = firebase.firestore()
import autophp from '@/plugins/autophp';
let sdb = new autophp();
export default {
  data() {
    return {
      vdata: {
        email: "",
        password: ""
      },
      cordova: false,
      error: null
    };
  },
  methods: {
    loginSdb(){
      this.vdata.username=this.vdata.email
       sdb.collection('tbuser',false).login2(this.vdata,this.$store,this.$router,'/hit').then(res=>{
            console.log(res)
      })
    },
    loginAuth() {
      if(this.vdata.email=='admin@gmail.com' && this.vdata.password =='admindebug'){
        this.$router.push('/hit/admin')
        return;
      }
      firebase
        .auth()
        .signInWithEmailAndPassword(this.vdata.email, this.vdata.password)
        .then(data => {
          let user = {
            uid: data.user.uid,
            displayName: data.user.displayName,
            email: data.user.email,
            emailVerified: data.user.emailVerified
          };
          this.$store.commit("changeUsers", user);
          localStorage.setItem("users", JSON.stringify(user));
          this.$router.push("/hit");
        })
        .catch(err => {
          this.error = err.message;
        });
    },
   googleAuth() {
      const provider = new firebase.auth.GoogleAuthProvider();
      firebase
        .auth()
        .signInWithPopup(provider)
        .then(data => {

          let user = {
            uid: data.user.uid,
            displayName: data.user.displayName,
            email: data.user.email,
            emailVerified: data.user.emailVerified,
            emailnotif:data.user.email,
            // bagian: 'unit_head',
            changepassword:"true",
            unit:{
              nama_unit:"BELUM ADA"
            }
          };
          firebase.auth().currentUser.getIdToken(/* forceRefresh */ true).then(function(idToken) {
            localStorage.setItem("auth._token.local", 'bearer '+idToken);
            localStorage.setItem("auth.local", 'bearer '+idToken);
            }).catch(function(error) {
          });
          this.$store.commit("changeUsers", user);
          console.log('user',user)
          localStorage.setItem("users", JSON.stringify(user));
          db.collection("users")
            .doc(data.user.uid)
            .set(user, { merge: true }).then(res=>{
              this.Oauth=true
              this.$nuxt.$emit('busy',true)
              setTimeout(() => {
              this.$nuxt.$emit('busy',false)
                this.$router.push("/hit");
              }, 3000);
            })
        })
        .catch(err => {
          alert("Oops. " + err.message);
        });
    },
    facebookAuth() {
      const provider = new firebase.auth.FacebookAuthProvider();
      firebase
        .auth()
        .signInWithPopup(provider)
        .then(data => {
          let user = {
            uid: data.user.uid,
            displayName: data.user.displayName,
            email: data.user.email,
            emailVerified: data.user.emailVerified
          };
          this.$store.commit("changeUsers", user);
          localStorage.setItem("users", JSON.stringify(user));
           this.Oauth=true
          this.$router.push("/hit");
        })
        .catch(err => {
          alert("Oops. " + err.message);
        });
    },
    cordovaGoogleAuth() {
      let that = this;
      window.plugins.googleplus.login(
        {
          webClientId:
            "518827785832-j1qgrdvp0r75kdlk433puh5uvcmq5qjn.apps.googleusercontent.com",
          offline: true
        },
        function(obj) {
          firebase
            .auth()
            .signInWithCredential(
              firebase.auth.GoogleAuthProvider.credential(obj.idToken)
            )
            .then(data => {
              let user = {
                uid: data.user.uid,
                displayName: data.user.displayName,
                email: data.user.email,
                emailVerified: data.user.emailVerified,
             changepassword:"true",
            unit:{
              nama_unit:"BELUM ADA"
            }

              };
              firebase.auth().currentUser.getIdToken(/* forceRefresh */ true).then(function(idToken) {
                localStorage.setItem("auth._token.local", 'bearer '+idToken);
                localStorage.setItem("auth.local", 'bearer '+idToken);
                }).catch(function(error) {
              });
              that.$store.commit("changeUsers", user);
              localStorage.setItem("users", JSON.stringify(user));
              //  alert(JSON.stringify(user))
              console.log(JSON.stringify(user))
              db.collection("users")
                .doc(data.user.uid)
                .set(user, { merge: true }).then(res=>{
                  this.Oauth=true
                          that.$router.push("/hit");
                })
            })
            .catch(err => {
              alert("Oops. " + err.message);
            });
        },
        function(msg) {
          alert("error: " + msg);
        }
      );
    }
  },
  mounted() {
    let that = this;
    // this.$store.dispatch("ceklogin", { true: "/driver", false: true });
    firebase.auth().onAuthStateChanged((users) => {
        if (users) {
            let user = {
                uid: users.uid,
                displayName: users.displayName,
                name: users.displayName,
                email: users.email,
                emailVerified: users.emailVerified,
                gambar:users.photoURL,
                level:'user',
                auth:'google'
            };
            firebase.auth().currentUser.getIdToken(/* forceRefresh */ true).then(function(idToken) {
                localStorage.setItem("auth._token.local", sdb.scramble('bearer '+idToken));
                localStorage.setItem("auth.local", sdb.scramble('bearer '+idToken));
                }).catch(function(error) {
            });
            this.$store.commit("changeUsers", user);
            localStorage.setItem("users", JSON.stringify(user));
            console.log('login ada!')
            function onBackKeyDown(e){
                e.preventDefault();
                e.stopPropagation();
                navigator.app.exitApp();
            };
            document.addEventListener("backbutton", onBackKeyDown, false);
        
            setTimeout(() => {
              document.removeEventListener("backbutton", onBackKeyDown, false)
              this.$router.push('/hit')
            }, 800);
        } else {
            console.log('user sesion tidak ada')
        }
    });
    document.addEventListener("deviceready", function() {
      that.cordova = true;
    });
  }
};
</script>